body {
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  font-style: normal;
  color: #444444;
  background-color: #ffffff;
}
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  color: inherit;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.button {
  display: block;
  padding: 10px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  background-color: #68b738;
  border: 1px solid #68b738;
}
@media (min-width: 768px) {
  font-size: 20px;
  line-height: 20px;
}
.burger-button {
  position: relative;
  box-sizing: content-box;
  width: 24px;
  height: 19px;
  padding: 10px;
  background-color: transparent;
  border: none;
}
.burger-button__line {
  display: inline-block;
  width: 24px;
  height: 2px;
  vertical-align: middle;
  background-color: #222222;
}
.burger-button::before,
.burger-button::after {
  content: "";
  position: absolute;
  left: 10px;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #222222;
}
.burger-button::before {
  top: 10px;
}
.burger-button::after {
  bottom: 10px;
}
.section-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: #111111;
}
.section-title--mini {
  font-size: 24px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 60px;
  }
  .section-title--mini {
    font-size: 36px;
    line-height: 36px;
  }
}
.statistics-list {
  display: flex;
  flex-wrap: wrap;
}
.statistics-list__item {
  position: relative;
  width: 123px;
  margin-right: auto;
  margin-bottom: 40px;
  padding: 15px 17px;
