.radio__input {
  display: none;
}
.radio__label {
  position: relative;
  display: flex;
  align-items: center;
}
.radio__label::before {
  content: "";
  display: inline-block;
  width: 30px;
  height: 30px;
  margin-right: 16px;
  border: 1px solid #e6e6e6;
  border-radius: 50%;
}
.radio__input:checked + .radio__label::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 9px;
  transform: translateY(-50%);
  background-color: #68b738;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .radio__label::before {
    width: 40px;
    height: 40px;
  }
  .radio__input:checked + .radio__label::after {
    left: 11px;
    width: 18px;
    height: 18px;
  }
}
.checkbox__input {
  display: none;
}
.checkbox__input + .checkbox__label::before {
  content: "\2714";
  display: inline-block;
  height: 30px;
  width: 30px;
  margin-right: 16px;
  font-size: 20px;
  font-weight: 900;
  line-height: 22px;
  text-align: center;
  vertical-align: middle;
  color: transparent;
  border: 1px solid #e6e6e6;
  transition: color ease 0.3s;
}
.checkbox__input:checked + .checkbox__label::before {
  color: #68b738;
}
@media (min-width: 768px) {
  .checkbox__input + .checkbox__label::before {
    width: 40px;
    height: 40px;
    font-size: 35px;
  }
}
